import React from 'react';
import { Row, Col } from 'antd';
import './style.scss';

export const StatisticBar = ({ amount, paidAmt, overPaidAmt, remainAmt, finalOverPaidAmt, finalRemainAmt }) => (
  <Row gutter={24} className="statistic-bar">
    <Col xs={24} sm={24} md={8} lg={8} xl={8}>
      <Row>
        <Col span={12} className="title">S tin phi thu k:</Col>
        <Col span={12} className="result">{amount || 0}</Col>
        <Col span={12} className="title">S tin  thu k:</Col>
        <Col span={12} className="result">{paidAmt || 0}</Col>
      </Row>
    </Col>
    <Col xs={24} sm={24} md={8} lg={8} xl={8}>
      <Row>
        <Col span={12} className="title">S tin thu tha k:</Col>
        <Col span={12} className="result">{overPaidAmt || 0}</Col>
        <Col span={12} className="title">S tin cn phi thu k:</Col>
        <Col span={12} className="result">{remainAmt || 0}</Col>
      </Row>
    </Col>
    <Col xs={24} sm={24} md={8} lg={8} xl={8}>
      <Row>
        <Col span={12} className="title">S tin thu tha cui k:</Col>
        <Col span={12} className="result">{finalOverPaidAmt || 0}</Col>
        <Col span={12} className="title">S tin cn phi thu cui cng:</Col>
        <Col span={12} className="result">{finalRemainAmt || 0}</Col>
      </Row>
    </Col>
  </Row>
);



