import { request, requestDownload } from 'utils';

const prefix = '/api/finance';

export const getTuititionDeptStatistic = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  receivableTypeId,
  bySemester,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/Receipt/BrowseDebt',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      receivableTypeId,
      bySemester,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });

export const exportClassFeeDebtReport = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  receivableTypeId,
  bySemester,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  requestDownload({
    prefix,
    url: '/Receipt/ExportReportSummarizeFeeDebt',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      receivableTypeId,
      bySemester,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    defaultFileName: 'ClassDebtReport.xlsx',
  });

export const exportStudentFeeDeptByReceiptReport = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  receivableTypeId,
  bySemester,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  requestDownload({
  prefix,
  url: '/Receipt/ExportExcelStudentFeeDebt',
  method: 'POST',
  params: {},
  data: {
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    classId,
    semesterId,
    yearEduId,
    receivableTypeId,
    bySemester,
    page,
    pageSize,
    orderBy,
    isPagingEnabled
  },
  defaultFileName: 'StudentFeeDebtByReceiptReport.xlsx',
});

export const exportStudentFeeDeptByClassReport = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  receivableTypeId,
  bySemester,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  requestDownload({
    prefix,
    url: '/Receipt/ExportReportStudentFeeDebt',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      receivableTypeId,
      bySemester,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    defaultFileName: 'StudentFeeDeptByClassReport.pdf',
  });



