import React from 'react';
import { BtnView } from 'components/common/button';

export const getColumConfig = that => (
  [
    {
      title: 'M SV',
      dataIndex: 'studentCode',
      key: 'studentCode',
      align: 'center'
    },
    {
      title: 'H v tn',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Tn lp',
      dataIndex: 'className',
      key: 'className',
      align: 'center'
    },
    {
      title: 'Thiu tha k trc',
      dataIndex: 'previousAmt',
      key: 'previousAmt',
      align: 'center'
    },
  
    {
      title: 'Phi np k',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    },
    {
      title: ' np k',
      dataIndex: 'paidAmt',
      key: 'paidAmt',
      align: 'center'
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      className: 'action-column',
      align: 'center',
      render: () => (
        <BtnView onClick={that.onViewRegistedCredits} />
      ),
    },
  ]
)


