import React, { Component } from 'react';
import { Row, Col, Form, Radio } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import { getReceivable } from 'services/receivable';
import { getSemesters } from 'services/semesterService';
import './style.scss';

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    const { form, dataForm } = this.props;
    e.preventDefault();
    form.validateFields((err, values) => {
      dataForm(values);
    });
  };

  render() {
    const {
      form: { getFieldDecorator, getFieldValue, setFieldsValue },
    } = this.props;
    const yearEduId = getFieldValue('yearEduId');
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
    };
    return (
      <Form
        className="form-inline-label search-form"
        layout="vertical"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24} type="flex" align="stretch">
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <Col span={5}>
            <Form.Item label="Nm hc">
              {getFieldDecorator('yearEduId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn!',
                  },
                ],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Hc k">
              {getFieldDecorator('semesterId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn!',
                  },
                ],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item label="Khon np">
              {getFieldDecorator('receivableTypeId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lng chn!',
                  },
                ],
              })(<SelectAjax setFieldsValue={setFieldsValue} service={getReceivable} />)}
            </Form.Item>
          </Col>
          <Col span={5} className="view-type">
            <Form.Item>
              {getFieldDecorator('bySemester', {
                initialValue: true,
              })(
                <Radio.Group>
                  <Radio value>Theo hc k</Radio>
                  <Radio value={false}>Ton kha hc</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={4} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



